@import "normalize";
@import "adaptive_fonts";
@import "fonts";
@import "headings";

* {
  box-sizing: border-box;
}

body {
  font-family: 'Stratos LC', sans-serif;
  font-style: normal;
  font-weight: 400;
  @extend .adaptive_16px;
  line-height: 135%;
  min-width: 320px;
  min-height: 100vh;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: $IVORY;
}

.page {
  flex: 1 0 auto;
}


.uppercase {
  text-transform: uppercase;
}

button, a {
  cursor: pointer;
}

.line-clamp {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  box-orient: vertical;
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.non-scrollbar {
  &::-webkit-scrollbar {
    display: none;
  }

  -ms-overflow-style: none;
  scrollbar-width: none;
}

.standart-padding-left {
  padding-left: 100px;
  @media screen and (max-width: $notebook_start) {
    padding-left: auto-calculate($notebook_start, $notebook, 100px, 50px);
  }
  @media screen and (max-width: $notebook) {
    padding-left: auto-calculate($notebook_start, $tablet, 50px, 20px);
  }
  @media screen and (max-width: $tablet) {
    padding-left: 20px;
  }
}

.standart-padding-right {
  padding-right: 100px;
  @media screen and (max-width: $notebook_start) {
    padding-right: auto-calculate($notebook_start, $notebook, 100px, 50px);
  }
  @media screen and (max-width: $notebook) {
    padding-right: auto-calculate($notebook_start, $tablet, 50px, 20px);
  }
  @media screen and (max-width: $tablet) {
    padding-right: 20px;
  }
}


.standart-padding-both {
  @extend .standart-padding-left;
  @extend .standart-padding-right;
}

.adaptive-non {
  @media screen and (max-width: $tablet) {
    display: none;
  }
}

.adaptive {
  display: none;
  @media screen and (max-width: $tablet) {
    display: initial;
  }
}

.overflow-full-width-container {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, .1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 320px;
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
  transition: .5s;
  z-index: $modal_z;
  outline: none;

  .modal {
    cursor: default;
    transition: .3s;
    transform: translateY(20vh);
  }

  &.show {
    visibility: visible;
    opacity: 1;

    .modal {
      transform: translateY(0);
    }
  }
}

.dn {
  display: none;
}

.bold{
  font-weight: bold;
}

